@charset "utf-8";

/*  进度相关      */
.pro_bars {
    display: none;
    position: absolute;
    top: 110px;
    left: 50%;
    margin-left: -205px;
    height: 30px;
    width: 404px;
    background: url(../images/progressbac.png) no-repeat;
    z-index: 55;
  
    span {
        position: absolute;
        display: block;
        top: 2px;
        left: 2px;
        height: 24px;
        width: 0%;
        text-align: center;
        border-radius: 15px;
    }
    
    .progressbar-gif{
        background:url(../images/progress2.gif) no-repeat;
    }
    
    .progressbar-static{
        background:url(../images/progress_static.png) no-repeat;
    }
    
    .pro_labels {
        position: absolute;
        top: -36px;
        margin: 2px;
        height: 28px;
        line-height: 24px;
        font-size: 16px;
        width: 400px;
        color: #fff;
        text-align: center;
    }
    
    .process-label{
        position: absolute;
        top: -40px;
        color: #fff;
        width: 400px;
        font-size: 18px;
        text-align: center;
    }
    
    .success-icon{
        position: absolute;
        width: 20px;
        height: 20px;
        right: -50px;
        top: 5px;
        background: url(../images/allfinish.png) no-repeat;
    }
    
    .fail-icon{
        position: absolute;
        width: 20px;
        height: 20px;
        right: -50px;
        top: 5px;
        background: url(../images/failicon.png) no-repeat;
    }
}
#progressbar_one{
  position: fixed;
  top: 27%;
}





